@import 'vars';
@import 'mixins';

.#{$prefix}video-container {
  @include mobile {
    position: relative;
    padding-bottom: 56.25% !important;
    width: 100%;
    height: 0;

    margin: 0 10px; } }

.#{$prefix}video {
  width: 556px;
  height: 286px;

  &--small {
    width: 326px;
    height: 184px; }

  @include mobile {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; } }
